

<template>
  <div class="visitor-log">
    <van-sticky :offset-top="0.00000001">
      <van-nav-bar title="访客记录" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <!-- 主页面 -->
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-empty
        v-if="this.data.length == 0"
        description="当前无访客"
        style="height: 80vh"
      />
      <van-list
        v-else
        @load="onLoad"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        style="min-height: calc(100vh - 46px)"
      >
        <div
          class="custom-list"
          v-for="(item, i) in data"
          :key="i"
          @click="handleQuery(item)"
        >
          <van-swipe-cell>
            <van-cell size="large">
              <!-- 自定义标题 -->
              <template #title>
                <div class="title">
                  <van-icon
                    class="iconfont"
                    class-prefix="icon"
                    :name="item.visitType == 1 ? 'VIPfangke' : 'linshifangke'"
                  />
                  <span>{{
                    { 1: '贵宾访客', 3: '临时访客' }[item.visitType]
                  }}</span>
                </div>
              </template>
              <!-- 自定义主要内容 -->
              <template #label>
                <div class="custom-title-detail">
                  <div>
                    <span>访客姓名：</span>
                    <span class="van-ellipsis"> {{ item.visitorName }}</span>
                  </div>
                  <div>
                    <span>访问时间：</span>
                    <span
                      >{{ item.visitStartTime }} - {{ item.visitEndTime }}</span
                    >
                  </div>
                </div>
              </template>
              <template #default>
                <span :class="item.sts">
                  <i />
                  {{ visitorStsList[item.sts] }}
                </span>
              </template>
            </van-cell>
            <template #right>
              <van-button
                square
                type="danger"
                text="删除"
                @click="handleDel(item, i)"
              />
            </template>
          </van-swipe-cell>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { qryVisitorInfoList, deleteVisitorInfo } from '@/api/visitor' // 获取访客记录
export default {
  name: 'VisitorLog',
  data() {
    return {
      data: [],
      loading: false,
      finished: false,
      refreshing: false,
      page: {
        page: 1,
        size: 8
      },
      visitorStsList: {
        YYZ: '预约中',
        YYTY: '预约同意',
        YYBTY: '未同意',
        YDF: '已到访',
        YLK: '已离开',
        JJTX: '拒绝通行'
      } //状态列表
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back(-1)
    },
    handleQuery(item) {
      // 查看访客记录
      //预约中才可以修改
      const disabled = !['YYZ', 'YYTY'].includes(item.sts)
      if (item.sts !== 'YYZ') {
        this.$toast({
          duration: 3000,
          message: '该访客预约单状态并非【预约中】，不可进行操作'
        })
      }
      switch (item.visitType) {
        case '1':
          this.$router.push({
            name: 'VIPappointment',
            params: { disabled, vipVisitId: item.visitorInfoId }
          })
          break
        case '3':
          this.$router.push({
            name: 'temporaryVisitorReg',
            params: {
              disabled,
              isToOpenId: true,
              temporaryVisitId: item.visitorInfoId
            }
          })
          break
        default:
          this.$toast({
            message: '数据失效'
          })
          break
      }
    },
    handleDel(item, i) {
      this.$dialog
        .confirm({
          title: '请确认是否删除此条信息？'
        })
        .then(() => {
          deleteVisitorInfo({
            visitorInfoId: item.visitorInfoId,
            createName: this.myUserInfo.name
          }).then((res) => {
            if (res.code === 200) {
              this.$toast({
                message: res.msg
              })
              this.data.splice(i, 1)
            } else {
              this.$toast({
                message: res.msg
              })
            }
          })
        })
        .catch(() => {
          // on cancel
        })
    },
    onLoad() {
      // 获取访客记录一览
      qryVisitorInfoList({
        // toOpenid: this.myUserInfo.openId,
        phoneNbr: this.myUserInfo.phoneNbr,
        pageNo: this.page.page,
        pageSize: this.page.size
      })
        .then((res) => {
          this.loading = false
          if (res.code === 200) {
            this.page.page += 1
            if (this.refreshing) {
              this.data = []
              this.refreshing = false
            }
            this.data = this.data.concat(
              res.data.row.map((item) => {
                return {
                  ...item,
                  visitStartTime: this.$util.format(
                    new Date(item.visitStartTime.replace(/-/g, '/')),
                    'yyyy-MM-dd hh:mm'
                  ),
                  visitEndTime: this.$util.format(
                    new Date(item.visitEndTime.replace(/-/g, '/')),
                    'yyyy-MM-dd hh:mm'
                  ),
                  visitorName: String(
                    eval('(' + item.visitorPersonArr + ')').map(
                      (item1) => item1.visitorName
                    )
                  )
                }
              })
            )
            if (this.data.length === res.data.total) {
              this.finished = true
            }
          } else {
            this.$toast.fail({
              message: res.msg
            })
          }
        })
        .catch(() => {
          this.loading = false
        })
    },
    onRefresh() {
      this.finished = false
      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true
      this.page.page = 1
      this.onLoad()
    }
  },
  async created() {
    this.onRefresh()
  },
  computed: mapState({
    myUserInfo: (state) => state.user.userInfo
  })
}
</script>

<style lang="less">
.visitor-log {
  div.custom-list {
    padding: 11px 8px 0;
    text-align: right;
    position: relative;
    transition: height 0.3s ease-in-out;
    > div {
      box-shadow: 0 0 10px -7px #d5d5d5;
      .van-cell {
        border-radius: 6px;
        .van-cell__title {
          flex: inherit;
          text-align: left;
          max-width: 275px;
          > div.title {
            display: flex;
            align-items: center;
            i {
              font-size: 17px;
              &.icon-richangfangke {
                margin-left: -2.3px;
                margin-right: -6px;
              }
            }
            > span {
              margin-left: 3px;
              font-size: 14px;
              font-weight: 550;
              color: #525256;
            }
          }
        }
        .van-cell__label {
          margin: 10px 0 5px;
          line-height: 20px;
          .custom-title-detail > div {
            span {
              white-space: nowrap;
              &:first-child {
                display: inline-block;
                width: 60px;
              }
            }
            display: flex;
            align-items: center;
            font-size: 12px;
          }
        }
        .van-cell__value {
          span {
            font-size: 10px;
            width: 100%;
            white-space: nowrap;
            i {
              display: inline-block;
              width: 5px;
              height: 5px;
              background: #666;
              border-radius: 50%;
            }
          }
          span.YYTY {
            color: #07c160;
            i {
              background: #07c160;
            }
          }
          span.YYZ {
            color: #ffaf60;
            i {
              background: #ffaf60;
            }
          }
          span.YYBTY {
            color: #ee0a24;
            i {
              background: #ee0a24;
            }
          }
          span.YDF {
            color: #ebd671;
            i {
              background: #ebd671;
            }
          }
          span.YLK {
            color: #5ee6eb;
            i {
              background: #5ee6eb;
            }
          }
          span.JJTY {
            color: #f473b9;
            i {
              background: #f473b9;
            }
          }
        }
      }
      .van-swipe-cell__right {
        right: -1px;
        button:last-child {
          border-top-right-radius: 6px;
          border-bottom-right-radius: 6px;
        }
      }
    }
  }
}
</style>

